import React from "react";
import "./list.css";
import "@lottiefiles/lottie-player";
import ListForm from "./ListForm";
import { NavLink } from "react-router-dom";
import "animate.css";
function List() {
  return (
    <>
      <div className=" main-list">
        <div className=" dark first-child ">
          <div
            className="info-wi flex items-center p-4 mb-4 text-sm text-green-800 border border-green-300 rounded-lg bg-green-50 dark:bg-gray-800 dark:text-green-400 dark:border-green-800"
            role="alert"
          >
            <svg
              className="flex-shrink-0 inline w-4 h-4 me-3"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              fill="currentColor"
              viewBox="0 0 20 20"
            >
              <path d="M10 .5a9.5 9.5 0 1 0 9.5 9.5A9.51 9.51 0 0 0 10 .5ZM9.5 4a1.5 1.5 0 1 1 0 3 1.5 1.5 0 0 1 0-3ZM12 15H8a1 1 0 0 1 0-2h1v-3H8a1 1 0 0 1 0-2h2a1 1 0 0 1 1 1v4h1a1 1 0 0 1 0 2Z" />
            </svg>
            <span className="sr-only">Info</span>
            <div className="animate__animated animate__headShake anima">
              <span className="font-large  ">
                Click me to show yor list After you add it
              </span>
              <NavLink
                to="/show"
                className="font-medium items-center text-blue-600 hover:underline "
              >
                {" "}
                "Here"{" "}
              </NavLink>
            </div>
          </div>
        </div>
        <div className="second-child">
          <div>
            <ListForm />
          </div>

          <div>
            <lottie-player
              autoplay
              loop
              mode="normal"
              src="https://lottie.host/fb481f47-90f6-4117-9df5-e2be61dc14f7/LhbmN8Mi4d.json"
              style={{ width: " 320px" }}
            ></lottie-player>
          </div>
        </div>
      </div>
    </>
  );
}

export default List;
